import Link from "next/link";

const User = () => {
  return (
    <div>
      <h1 className='mt-10 font-mono font-bold text-red-400 text-3xl'>Users Dashboard</h1>
      <ul className='list-[circle]-[revert]'>
        <li className='font-white bg-amber-400 underline decoration-2 decoration-wavy'><Link href="/dashboard/users/1">User 1</Link></li>
        <li className='font-white bg-amber-400 underline decoration-2 decoration-wavy'><Link href="/dashboard/users/2">User 2</Link></li>
        <li className='font-white bg-amber-400 underline decoration-2 decoration-wavy'><Link href="/dashboard/users/3">User 3</Link></li>
        <li className='font-white bg-amber-400 underline decoration-2 decoration-wavy'><Link href="/dashboard/users/4">User 4</Link></li>
      </ul>

      <button type="button" className='rounded bg-blue-500 cursor-pointer p-1 mt-3'>Click to get More Users</button>
    </div>
  )
}

export default User
